(function () {

	'use strict';

	var List = require('../list'),
		Hash = require('../hash'),
		peace = {
			state: new Hash(),
			channel: function (channel) {
				return {
					spark: function (Spark, spark) { //console.log('peace.channel().spark()');
						var state;
						if (state = peace.state.fetch(channel)) return state;
						peace.state.store(channel, (
							state = peace.spark(Spark, spark)
								.channel(channel)))
						return state;
					}
				};
			},
			spark: function (Spark, spark) { //console.log('peace.spark()');
				var state;
				if (state = peace.state.fetch('spark')) return state;
				Spark = (Spark || false).constructor === Function ? Spark : function () {};
				spark = (spark || false).constructor === Object ? spark : {};
				peace.state.store('spark', (
					state = (function (Spark, spark) {
							var url = spark.url || window.location.href,
								options = spark.options || {};
							return new Spark(url, options);
						}(Spark, spark))));
				return state;
			},
			verbs: require('../verbs'),
			merge: (function () {
				var has = Object.prototype.hasOwnProperty;
				return function (alpha, omega) {
					var key, verbs = {};
					/*
						Defensive because omega can
						be passed in by a consumer. We may
						as well examine alpha the same way
					*/
					alpha = (alpha || false).constructor === Object ? alpha : {};
					omega = (omega || false).constructor === Object ? omega : {};
					for (key in alpha) {
						if (has.call(alpha, key) === true) {
							verbs[key] = (
								has.call(omega, key) === true) ? omega[key] : alpha[key];
						}
					}
					return verbs;
				};
			}()),
			generateMessage: function generateMessage(one, two) { //console.log('generateMessage', one, two);
				if ((one || false).constructor === String) {
					if ((two || false).constructor === Object) {
						return { resource: one ? one : '/', query: two };
					} else {
						return { resource: one ? one : '/' };
					}
				} else {
					if ((one || false).constructor === Object) {
						return { resource: '/', query: one };
					} else {
						return { resource: '/' };
					}
				}
			},
			listenerManagerFactory: function () { //console.log('listenerManagerFactory');
				var listeners = {},
					broadcast = {};
				return function ListenerManager(event, spark) {
					var self = this;
					if ((event || false).constructor === String) {
						self.then = function (handle) {
							var l;
							if ((handle || false).constructor === Function) {
								(l = (listeners[event] || (listeners[event] = [])))
									.push(handle);
								if (l.length === 1) {
									spark.on(event, broadcast[event] = function (m) {
										var i = 0,
											j = l.length;
										for (i, j; i < j; i = i + 1) {
											l[i](m);
										}
									});
								}
								self.stop = function () {
									var i = 0,
										j = l.length;
									while (i < j) {
										if (l[i] === handle) {
											l.splice(i, 1);
										} else {
											i = i + 1;
										}
									}
									if (l.length === 0) {
										spark.off(event, broadcast[event]);
										delete listeners[event];
										delete broadcast[event];
									}
									return self;
								};
								return self;
							}
						};
					}
				};
			}
		};

	module.exports = peace;

}());


