(function () {

	'use strict';

	function List() {
		var all = {};
		this.has = function (key) {
			return (all[key] || []).length > 0;
		};
		this.all = function () {
			return all;
		};
	}
	List.prototype.fetch = function (key) { //console.log('fetch', key);
		return this.all()[key];
	};
	List.prototype.store = function (key, value) { //console.log('store', key); //, value);
		(this.all()[key] || (this.all()[key] = []))
			.push(value);
	};
	List.prototype.purge = function (key, value) { //console.log('purge', key); //, value);
		var list, n;
		if (list = this.all()[key]) {
			n = 0;
			while (n < list.length) {
				if (list[n] === value) { //console.log(n);
					list.splice(n, 1);
					continue;
				}
				n = n + 1;
			}
			if (this.has(key)) return true;
			delete this.all()[key];
		}
		return true;
	};

	module.exports = List;

}());


