(function () {

	'use strict';

	function Hash() {
		var all = {};
		this.has = function (key) {
			for (key in all) return true;
			return false;
		};
		this.all = function () {
			return all;
		};
	}
	Hash.prototype.fetch = function (key) { //console.log('fetch', key);
		return this.all()[key];
	};
	Hash.prototype.store = function (key, value) { //console.log('store', key); //, value);
		this.all()[key] = value;
	};
	Hash.prototype.purge = function (key) { //console.log('purge', key);
		delete this.all()[key];
	};

	module.exports = Hash;

}());


